.advan {
  background-color: #fff5f6;
  padding-bottom: 80px;

  @media screen and (min-width: 1280px) {
    padding-bottom: 120px;
  }
}

.advan-list {
  justify-content: start;

  @media screen and (min-width: 480px) {
    display: flex;
  }

  /* @media screen and (min-width: 1280px) {
    width: ;
  } */
}

.advan-list_element:not(:last-child) {
  margin-bottom: 32px;
}

.advan-list_element {
  font-weight: 700;
  font-size: 12px;
  line-height: 170%;
}

.advan-list_text {
  max-width: 230px;
}

.advan-list_meaning {
  font-family: 'Titan One';
  font-weight: 400;
  font-size: 36px;
  line-height: 1.38;
  letter-spacing: 0.04em;
  text-transform: uppercase;

  color: var(--primary-color);

  display: block;
  margin-top: 16px;
  margin-bottom: 20px;
}

/* PNG изображения секций */
/* 1x-2x */

.picture_first::before {
  content: ' ';
  display: block;
  background-image: url('../images/advantages-1-x1.png');
  width: 33px;
  height: 50px;
  background-size: 33px 50px;
}

@media (min-device-pixel-ratio: 2),
  (min-resolution: 192dpi),
  (min-resolution: 2dppx) {
  .picture_first::before {
    background-image: url('../images/advantages-1-x2.png');
  }
}

